@mixin big-desktop {
    @media (max-width: (1440px)) {
        @content;
    }
}

@mixin small-desktop {
    @media (max-width:(1200px)) {
        @content;
    }
}

@mixin tablet {
    @media (max-width: (1023px)) {
        @content;
    }
}

@mixin small-tablet {
    @media (max-width: (768px)) {
        @content;
    }
}

@mixin mobile {
    @media (max-width: (576px)) {
        @content;
    }
}


