:root {
  --font-family: "Consolas", sans-serif;
  --black-color: #000;
  --white-color: #fff;
  --content-width: 738px;
  --container-offset: 20px;
  --container: calc(var(--content-width) + (var(--container-offset) * 2));
  --accent-color: #0062FF;
  --dark-color: #202326;
  --grey-color: #DCDCDC;
  --Open-sans: "OpenSans", sans-serif;
}

@font-face {
  font-family: "Consolas";
  src: url("../fonts/Consolas-Bold.woff2") format("woff2"), url("../fonts/Consolas-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
  font-style: bold;
}
@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSansRegular.woff2") format("woff2"), url("../fonts/OpenSansRegular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

body {
  margin: 0;
  min-width: 320px;
  font-family: Arial, Helvetica, sans-serif;
  background-color: var(--color-white);
  font-weight: 400;
}

.container {
  max-width: var(--container);
  padding: 0 var(--container-offset);
  margin: 0 auto;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.btn-reset {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.is-hidden {
  display: none !important;
}

.flex {
  display: flex;
}

.descr {
  font-family: var(--font-family);
  font-weight: 400;
}

.header {
  padding-top: 80px;
  padding-bottom: 64px;
}
@media (max-width: 1023px) {
