.advertisement {
  padding-top: 34px;
  padding-bottom: 36px;
  background-color: var(--grey-color);
}
@media (max-width: 1023px) {
  .advertisement {
    padding-top: 22px;
    padding-bottom: 20px;
  }
}
.advertisement__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 1023px) {
  .advertisement__container {
    align-items: normal;
  }
  .advertisement__container picture img {
    height: 200px;
  }
}
.advertisement__link {
  margin-bottom: 17px;
  padding-left: 64px;
  display: block;
  align-self: flex-start;
  font-family: Helvetica;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  color: var(--black-color);
