  }
}
.advertisement__link u {
  color: var(--accent-color);
}

.conclusion {
  padding-top: 45px;
  padding-bottom: 74px;
}
@media (max-width: 1023px) {
  .conclusion {
    padding-top: 35px;
    padding-bottom: 30px;
  }
}
.conclusion__title {
  margin: 0;
  margin-bottom: 26px;
  font-family: var(--font-family);
  font-size: 28px;
  font-weight: 400;
  line-height: 36px;
  color: var(--black-color);
}
@media (max-width: 1023px) {
  .conclusion__title {
    margin-bottom: 21px;
    font-size: 21px;
  }
}
.conclusion__descr {
  margin: 0;
  font-family: Arial;
  font-size: 16px;
  line-height: 24px;
  color: var(--dark-color);
}
@media (max-width: 1023px) {
  .conclusion__descr {
    font-size: 14px;
    line-height: 18px;
  }
}

.feedback {
  padding: 40px 0;
  background-color: var(--grey-color);
}
.feedback label {
  position: relative;
}
.feedback__descr {
  margin-bottom: 15px;
  display: block;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
}
.feedback__stars {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  margin-bottom: 30px;
}
.feedback__stars[data-total-star="1"] .feedback__star:nth-child(n+5) svg path, .feedback__stars[data-total-star="2"] .feedback__star:nth-child(n+4) svg path, .feedback__stars[data-total-star="3"] .feedback__star:nth-child(n+3) svg path, .feedback__stars[data-total-star="4"] .feedback__star:nth-child(n+2) svg path, .feedback__stars[data-total-star="5"] .feedback__star:nth-child(n+1) svg path {
  fill: #FFB400;
}
.feedback__star {
  cursor: pointer;
}
.feedback__star:hover, .feedback__star:hover ~ .feedback__star {
  transition: all 0.2s ease-in-out;
}
.feedback__star:hover svg path, .feedback__star:hover ~ .feedback__star svg path {
  fill: #4d4d4d;
}
.feedback__star:not(:last-child) {
  margin-left: 10px;
}
.feedback__form {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  row-gap: 20px;
  column-gap: 30px;
}
@media (max-width: 1023px) {
  .feedback__form {
    grid-template-columns: repeat(1, 1fr);
    row-gap: 10px;
    column-gap: 0px;
  }
}
.feedback__form .input {
  grid-row: 1;
  width: 100%;
}
@media (max-width: 1023px) {
  .feedback__form .input:first-child {
    grid-row: 1;
  }
  .feedback__form .input:nth-child(2) {
    grid-row: 2;
  }
}
.feedback__form .input input {
  padding: 18px 22px;
  width: 100%;
  border-radius: 6px;
  font-family: Arial;
  font-size: 14px;
  line-height: 15px;
  color: #222222;
}
.feedback__form .input input::placeholder {
  font-family: Arial;
  font-size: 14px;
  line-height: 14px;
  color: #777777;
}
.feedback__form .input input:focus {
  outline: 2px solid #7F7F7F;
}
.feedback__form .input input:hover {
  outline: 2px solid #7F7F7F;
}
.feedback__form .textarea {
  grid-row: 2;
  grid-column: 1/3 span;
  width: 100%;
}
@media (max-width: 1023px) {
  .feedback__form .textarea {
    grid-row: 4;
  }
}
.feedback__form .textarea textarea {
  padding: 10px;
  width: 100%;
  height: 120px;
  border-radius: 6px;
  resize: none;
}
.feedback__form .textarea textarea:focus {
  outline: 2px solid #7F7F7F;
}
.feedback__form .textarea textarea:hover {
  outline: 2px solid #7F7F7F;
}
.feedback__form .textarea textarea::placeholder {
  padding-top: 5px;
  padding-left: 9px;
  font-family: Arial;
  font-size: 14px;
  line-height: 14px;
  color: #777777;
}
.feedback__form .form__btn {
  grid-row: 3;
  padding: 14px 70px;
  font-family: Arial;
  font-size: 14px;
  font-weight: bold;
  line-height: 16px;
  color: var(--white-color);
  background-color: var(--accent-color);
  transition: all 0.2s ease-in-out;
}
.feedback__form .form__btn:hover {
  background-color: rgba(0, 98, 255, 0.7);
}
@media (max-width: 1023px) {
  .feedback__form .form__btn {
    grid-row: 5;
    max-width: 280px;
  }
}
@media (max-width: 1023px) {
  .feedback__form .books {
    position: relative;
    grid-row: 3;
  }
}
.feedback__form .book-current {
  position: relative;
  padding: 18px 22px;
  width: 100%;
  border-radius: 6px;
  cursor: pointer;
  background-color: #fff;
}
.feedback__form .book-current .book__select {
  font-family: Arial;
  font-size: 14px;
  line-height: 15px;
  color: #777777;
}
.feedback__form .book-current:hover {
  outline: 2px solid #7F7F7F;
}
.feedback__form .book-current::after {
  position: absolute;
  top: 20px;
  right: 22px;
  content: "";
  height: 12px;
  width: 12px;
  background-image: url(../images/dropdown.svg);
  background-repeat: no-repeat;
  background-size: contain;
  transform: rotate(-180deg);
}
.feedback__form .books.books-active .book-current::after {
  top: 22px;
  transform: rotate(0deg);
}
.feedback__form .books.books-active .books-list {
  display: block;
  z-index: 10;
}
@media (max-width: 1023px) {
  .feedback__form .books.books-active .books-list {
    width: 100%;
  }
}
.feedback__form .books-list {
  position: absolute;
  margin-top: 10px;
  display: none;
}
.feedback__form .books-list .book-item {
  position: relative;
  padding: 17px 15px 17px 23px;
  cursor: pointer;
  background-color: var(--white-color);
}
.feedback__form .books-list .book-item .book-name {
  font-family: Arial;
  font-size: 14px;
  line-height: 15px;
  color: #222222;
}
.feedback__form .books-list .book-item:hover {
  background-color: #EDEDED;
}
.feedback__form .books-list .book-item:hover::after {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 7px;
  height: 100%;
  background-color: var(--accent-color);
}
.feedback .just-validate-error-label {
  position: absolute;
  font-size: 12px;
  top: 3px;
  left: 21px;
  color: red !important;
}

.posts {
